@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
	text-align: center;
	width: 85%;
	margin: 0 auto;
	max-width: 1920px;
}

body {
	overflow-x: hidden;
	position: relative;
	background-image: url(/sociallast/static/media/bg-pattern-top-desktop.0e1f3813.svg), url(/sociallast/static/media/bg-pattern-bottom-desktop.1ccf745a.svg);
	background-repeat: no-repeat, no-repeat;
	background-position: top left, bottom right;
}

@media (max-width: 600px) {
	body {
		background-image: url(/sociallast/static/media/bg-pattern-top-mobile.f55aa9cc.svg), url(/sociallast/static/media/bg-pattern-bottom-mobile.02b87d67.svg);
	}
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
